.btn-play-video {
  display: block;
  margin: 10px 10px 0;
  position: relative;
  font-weight: 700;
  padding-left: 20px;
}
.btn-play-video > span {
  position: absolute;
  width: 25px;
  height: 25px;
  top: 5px;
  left: 10px;
  margin: 0;
  border-radius: 50%;
  background: #f5f4f0;
  background: linear-gradient(to bottom, #f5f4f0 50%, #dcd8cd 85%);
  -pie-background-image: linear-gradient(to bottom, #f5f4f0 50%, #dcd8cd 85%);
  border: 1px solid #b5b2ae;
  box-shadow: 0 2px 2px -1px #ccc !important;
  text-indent: -9999px;
}
.btn-play-video > span:before {
  content: "";
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  border-left: 8px solid #3169A8;
  top: 5px;
  left: 9px;
}
