#breadcrumb {
  width: auto;
  background: #fff;
  border-bottom: 1px solid #e0e0e0;
  height: 55px;
  line-height: 55px;
  margin: -22px 0 35px;
  position: relative;
}
#breadcrumb:empty {
  display: none;
}
#breadcrumb li {
  float: left;
}
#breadcrumb li:nth-child(odd) {
  margin-right: 3px;
}
#breadcrumb li:nth-child(even) {
  margin-left: 3px;
}
#breadcrumb li.DimensionCrumb:empty {
  display: none;
}
#breadcrumb aside {
  display: inline-block;
  float: right;
  font-size: 0;
}
#breadcrumb aside a:not(:last-of-type)::after {
  content: "|";
  color: #999;
  box-shadow: 0 4px 5px #e0e0e0;
  margin: 0 10px;
}
#breadcrumb:after {
  border-radius: 20px;
  bottom: 0;
  -moz-box-shadow: 0 4px 5px #e0e0e0;
  -webkit-box-shadow: 0 4px 5px #e0e0e0;
  -o-box-shadow: 0 4px 5px #e0e0e0;
  content: "";
  height: 10px;
  left: 0;
  position: absolute;
  width: 100%;
  z-index: -1;
}
#breadcrumb a.previous:before {
  content: "\003c\00a0";
}
#breadcrumb a.next {
  left: initial !important;
  right: initial !important;
  top: initial !important;
}
#breadcrumb a.next:after {
  content: "\00a0\003e";
}
