.dropdown {
  position: absolute;
  z-index: 9999999;
  display: none;
}
.dropdown .dropdown-menu {
  min-width: 160px;
  max-width: 360px;
  list-style: none;
  background: #FFF;
  border: 1px solid #DDD;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  overflow: visible;
  padding: 4px 0;
  margin: 0;
}
.dropdown .dropdown-menu LI {
  list-style: none;
  padding: 0;
  margin: 0;
  line-height: 18px;
}
.dropdown .dropdown-menu LI > A {
  display: block;
  color: #555;
  text-decoration: none;
  line-height: 18px;
  padding: 3px 15px;
  white-space: nowrap;
}
.dropdown .dropdown-menu LI > A:hover {
  background-color: #08C;
  color: #FFF !important;
  cursor: pointer;
}
.dropdown .dropdown-menu LABEL {
  display: block;
  color: #555;
  text-decoration: none;
  line-height: 18px;
  padding: 3px 15px;
  white-space: nowrap;
}
.dropdown .dropdown-menu LABEL:hover {
  background-color: #08C;
  color: #FFF !important;
  cursor: pointer;
}
.dropdown .dropdown-menu li input[type=submit] {
  border: 0;
  background: 0 0;
  display: block;
  width: 100%;
  color: #346aa8 !important;
  text-decoration: none;
  line-height: 18px;
  padding: 3px 15px;
  white-space: nowrap;
  text-align: left;
}
.dropdown .dropdown-menu li input[type=submit]:hover {
  background-color: #08C;
  color: #FFF !important;
  cursor: pointer;
}
.dropdown .dropdown-menu .dropdown-divider {
  font-size: 1px;
  border-top: solid 1px #E5E5E5;
  padding: 0;
  margin: 5px 0;
}
.dropdown .dropdown-panel {
  min-width: 160px;
  max-width: 360px;
  list-style: none;
  background: #FFF;
  border: 1px solid #DDD;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  overflow: visible;
  padding: 4px 0;
  margin: 0;
  padding: 10px;
}
.dropdown .undo A {
  background-image: url(icons/arrow-curve-180-left.png);
}
.dropdown .redo A {
  background-image: url(icons/arrow-curve.png);
}
.dropdown .cut A {
  background-image: url(icons/scissors.png);
}
.dropdown .copy A {
  background-image: url(icons/document-copy.png);
}
.dropdown .paste A {
  background-image: url(icons/clipboard.png);
}
.dropdown .delete A {
  background-image: url(icons/cross-script.png);
}
.dropdown.dropdown-tip {
  margin-top: 8px;
}
.dropdown.dropdown-tip:before {
  position: absolute;
  top: -6px;
  left: 9px;
  content: '';
  border-right: 7px solid transparent;
  border-bottom: 7px solid rgba(0, 0, 0, 0.2);
  border-left: 7px solid transparent;
  display: inline-block;
}
.dropdown.dropdown-tip:after {
  position: absolute;
  top: -5px;
  left: 10px;
  content: '';
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 6px solid #FFF;
  display: inline-block;
}
.dropdown.dropdown-tip.dropdown-anchor-right:before {
  left: auto;
  right: 9px;
}
.dropdown.dropdown-tip.dropdown-anchor-right:after {
  left: auto;
  right: 10px;
}
.dropdown.dropdown-scroll .dropdown-menu {
  max-height: 358px;
  overflow: auto;
}
.dropdown.dropdown-scroll .dropdown-panel {
  max-height: 358px;
  overflow: auto;
}
.dropdown.has-icons LI > A {
  padding-left: 30px;
  background: no-repeat 8px center;
}
