}

.amazonmenu ul li{
  position: static; /* menu li items should have position set to static */
  height: 27px;
}

.amazonmenu > ul{
  background: white;
  /*border: 1px solid gray;*/
  border-radius: 5px;
  width: 23%; /* width of main menu */
  /*height: 400px; !* height of main menu *!*/
