  overflow: auto;
  position: relative;
  text-decoration: none;
  margin-right: 3px;
  word-break: break-word;
  text-align: right;
  text-justify: inter-word;
  font-family: Milo;
  font-size: 14px;
}

.amazonmenu > ul li i{
  font-size: large;
}
.amazonmenu > ul li.hassub.selected > span {
  color: red!important;
}

.amazonmenu > ul li.hassub > a::after{ /* Arrow to indicate this item has sub menu (LI elements with sub menus have a ".hassub" class). */
  /*border: 5px solid transparent;*/
  /*border-right-color: gray; !* Default color of arrow *!*/
  /*content: '';*/
  /*height: 0;*/
  /*position: absolute;*/
  /*left: 5px;*/
  /*top: 35%;*/
  /*width: 0;*/
}

.amazonmenu > ul li.hassub.selected > a span{ /* Style of arrow when corresponding sub menu is open (LI element has a "selected" class during this state */
  color: red;
}

.amazonmenu ul li a:hover, .amazonmenu ul li.hassub.selected > a{ /* style of hover and selected menu item links */
  /*background: #6e7071;*/
  color: #e6a203;
  font-size: 14.2px;
  /*-webkit-transition: color 1s, font-size 1s;*/
  /*-moz-transition: color 1s, font-size 1s;*/
  /*-o-transition: color 1s, font-size 1s;*/
  /*transition: color 1s, font-size 1s;*/
}

.amazonmenu ul li > div, .amazonmenu ul li > ul{ /* Sub menus (Could be a DIV or UL wrapper) style */
  /*background: rgba(255, 255, 255, 0.10);*/
  /*border: 1px solid gray;*/
  /*border-radius: 0 8px 8px 0;*/
  /*box-shadow: 2px 2px 2px gray;*/
  display: none;
  font-weight: normal;
  width: 260px;
  height: 360px;
  right: 100%;
  padding: 10px 0;
  position: absolute;
  top: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  z-index: 1000;
  background-image: -moz-linear-gradient(left, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1));
  background-image: -ms-linear-gradient(left, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1));
  background-image: -webkit-gradient(linear, 0 0, 100% 0, from(rgba(255, 255, 255, 0)), to(rgba(255, 255, 255, 1)));
  background-image: -webkit-linear-gradient(left, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1));
  background-image: -o-linear-gradient(left, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1));
  background-image: linear-gradient(left, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1));
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='rgba(255, 255, 255, 0)', endColorstr='rgba(255, 255, 255, 1)', GradientType=1);
}
