.ul-collapse{
    /*ul {*/
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;

}

.ul-collapse li {
    float: right;
}
.ul-collapse li#dir{
    float : left
}

.ul-collapse li a {
    display: block;
    /*color: white;*/
    color: #febe1b;
    text-align: center;
    /*padding: 14px 16px;*/
    text-decoration: none;

}
.ul-collapse li a:hover {
    color: #febe1b;
}

