.promotions .modal {
    text-align: center;
    padding: 0!important;
}

.promotions .modal:before {
    content: '';
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    margin-right: -4px;
}

.promotions .modal-dialog {
    display: inline-block;
    text-align: left;
    vertical-align: middle;
}

.promotions .promotions_modal .modal-content {
    background-color: transparent;
    box-shadow: none;
}
.promotions .promotions_modal .modal-content .modal-body {
    padding: 0;
}

.promotions .promotions_modal .modal-content .modal-body img {
    width: 70%
}

.promotions .promotions_modal .modal-dialog {
    text-align: center;
}


